.filter {
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 3px;
  padding: 1rem;
  box-shadow: 1px 1px 5px -2px rgba(30, 30, 30, 0.1); }

.filter label,
.filter input[type="radio"] + label,
.filter input[type="checkbox"] + label {
  display: block;
  margin-bottom: 5px; }

.filter .form-group {
  border-bottom: 1px solid #e6e6e6;
  margin: 0 -1rem 10px;
  padding: 0 1rem 10px; }

.filter .form-group:last-child {
  margin-bottom: 0;
  border-bottom: 0;
  padding-bottom: 0; }

input[type=radio],
input[type=checkbox] {
  opacity: 0;
  position: absolute; }

input[type=radio] + label,
input[type=checkbox] + label {
  display: inline-block;
  cursor: pointer;
  position: relative;
  padding-left: 30px; }
  input[type=radio] + label:before, input[type=radio] + label:after,
  input[type=checkbox] + label:before,
  input[type=checkbox] + label:after {
    content: '';
    display: block;
    position: absolute;
    top: 50%; }
  input[type=radio] + label:before,
  input[type=checkbox] + label:before {
    background: #fff;
    width: 20px;
    height: 20px;
    margin-top: -10px;
    border-radius: 3px;
    left: 0;
    border: 1px solid #ccc; }
  input[type=radio] + label:after,
  input[type=checkbox] + label:after {
    opacity: 0;
    visibility: hidden;
    width: 8px;
    height: 8px;
    left: 6px;
    margin-top: -4px;
    z-index: 2;
    background: #999;
    border-radius: 1px; }

input[type=radio] + label:before {
  border-radius: 10px; }

input[type=radio] + label:after {
  border-radius: 4px; }

input[type=checkbox] + label:after {
  background: transparent;
  content: "\f00c";
  font-family: icomoon;
  font-size: .8rem;
  width: 20px;
  height: 18px;
  top: 1px;
  left: 0;
  line-height: 20px;
  text-align: center;
  margin-top: 0px;
  color: #777; }

input[type=radio]:checked + label:after,
input[type=checkbox]:checked + label:after {
  opacity: 1;
  visibility: visible; }

input[type=radio]:focus + label:before,
input[type=checkbox]:focus + label:before {
  border-color: #999999; }

input[type=radio]:disabled + label,
input[type=checkbox]:disabled + label {
  opacity: .5;
  cursor: default; }

.custom-select {
  position: relative;
