.items-list {
  list-style: none;
  padding: 0;
  margin: 0 -.5rem;
  font-size: 0; }

.items-list__item {
  display: inline-block;
  vertical-align: top;
  margin-left: .5rem;
  margin-right: .5rem;
  margin-bottom: 15px;
  width: 30%;
  width: calc(33.333% - 1rem);
  font-size: 1rem;
  background: #fff;
  padding: 1rem;
  border: 1px solid #e6e6e6;
  box-shadow: 1px 1px 5px -2px rgba(30, 30, 30, 0.1); }

.items-count {
  text-transform: uppercase;
  color: #aaa;
  font-weight: bold;
  margin-bottom: 1rem; }

.items-wrapper {
  position: relative; }
  .items-wrapper:before, .items-wrapper:after {
    content: '';
    position: absolute;
    top: 200px;
    left: 50%;
    width: 40px;
    height: 40px;
    border: 10px solid #ddd;
    margin: -20px 0 0 -20px;
    border-radius: 30px;
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94); }
  .items-wrapper:after {
    border-color: #69ACC2;
    border-left-color: transparent;
    border-top-color: transparent;
    animation: spinner 1.75s ease-in-out infinite; }
  .items-wrapper.is-loading:before, .items-wrapper.is-loading:after {
    opacity: 1; }

@keyframes spinner {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(359deg); } }

.pagination {
